*{
  font-family: Helvetica, arial, sans-serif;
}


body{
  padding: 60px;
}


input{
  text-align: center;
  color: grey;
  font-size: 1.2rem;
  height: 35px;
  width: 100%;
  border: 1px solid black;
  border-radius: 35px;  
}

h2{
  float: none;
}

input:focus {
  outline: none;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}